
import Confirm from 'vux/src/components/confirm/index.vue'

import vPosition from '@/views/ucenter/position'
import {changeCurrentUserType} from '@/api/ucenter.js'
import {myinfo,findOneCompany} from '@/api/company.js'

export default {
components: {
vPosition,
Confirm
},
created(){

myinfo().then( res => {
if( res.data.result == 0 ) {
this.userInfo = res.data.datas
}

})
},
data () {
return {
showdialog: false,
visibledetails: false,
array: [1,2,3,0],
list: [
{
path: '/resume',
listname: '我的简历',
listclass: 'enterprise'
},
{
path: '/objective',
listname: '求职意向',
listclass: 'objective'
},
{
path: '',
listname: '隐私设置',
listclass: 'setup'
},
{
path: '',
listname: '我的伯乐',
listclass: 'bole'
},
{
path: '',
listname: '意见反馈',
listclass: 'write'
},
{
path: '',
listname: '关于我们',
listclass: 'about'
}
],
collectdata: {},
positiondata: {},
userInfo: {},
}
},
methods: {
showMony(){
this.$refs.mypicker.pickerShow = true;
this.flag = true
},

tocompany () {

let data = {
currentUserType: 2
}
changeCurrentUserType(data).then( res => {
if (res.data.result == 0) {
this.$router.push('/home/enterprise')
}
})



},
showcollect (val, flag) {

if (flag == 0) {
this.collectdata = {}
} else if (flag == 1) {  // 收藏接口
this.collectdata =  {}
}

this.$refs.collect.show(val)
},
showposition (val) {
this.$refs.position.show(val)
},

showdetail (i) {
if (i == 3) {
this.visibledetails = !this.visibledetails
}

}
}
}



