import {companyResume} from '@/api/ucenter.js'
export default {
    props: {
        collectdata: {}
    },
    created () {
        this.getcompany()
    },
    data () {
        return {
            visiblecollect: false,
            title: '',
            positionlist: {}
        }
    },
    methods: {
        show (val) {
            this.visiblecollect = true
            this.title = val
        },
        // 应聘职位列表
        getcompany () {
            companyResume().then(res => {
                if (res.data.result == 0) {
                   this.positionlist = res.data.datas.list
                   
                }
            })
        }
    }
}



